import * as yup from 'yup';
import { isValidPhoneNumber } from 'libphonenumber-js';
import _isEmpty from 'lodash.isempty';
import config from '../../../config';

export const validationAddNewSchema = yup.object().shape({
	email: yup
		.string()
		.required('Email is required')
		.email('Please enter a valid email address.'),
	firstName: yup.string().required('First Name is required'),
	lastName: yup.string().required('Last Name is required'),
	phone: yup
		.string()
		.test(
			'Phone Number Test',
			'Please enter a valid phone number',
			(value = '') => {
				if (_isEmpty(value)) return true;
				return isValidPhoneNumber(`+${value}`, config.locate.countryCode);
			}
		),
});

export const validationEditSchema = yup.object().shape({
	dataTreePropertyId: yup
		.string()
		.required('Please search and select a property'),
	buyerAgentFeePct: yup
		.number()
		.min(0.1, 'Buyer Agent Fee must be greater than or equal to 0.1')
		.max(10, 'Buyer Agent Fee must be less than or equal to 10')
		.required('Buyer Agent Fee is required'),
	listingAgentFeePct: yup
		.number()
		.min(0.1, 'Listing Agent Fee must be greater than or equal to 0.1')
		.max(10, 'Listing Agent Fee must be less than or equal to 10')
		.required('Listing Agent Fee is required'),
	yearBuilt: yup
		.string()
		.required('Year Built is required')
		.matches(/^\d{4}$/, 'Year built must be a number and format like 1999')
});
